const navReducer = (state, action) => {
    if (state === undefined || action.type === "NAV_DEFAULT") {
        return [{ to: "/registration", text: "" }, { to: "/login", text: "" },]
    }
    if (action.type === "NAV_ADMIN") {
        return [{ to: "/diet", text: "" }, { to: "/food", text: " " }, {to: "/profile", text: ""}, { to: "/admin", text: "" }]
    }
    if (action.type === "NAV_USER") {
        return [{ to: "/diet", text: "" }, { to: "/food", text: " " }, {to: "/profile", text: ""}]
    }
    return state
}

export default navReducer